import { Routes } from "@angular/router";
import { SalesComponent } from "./sales/sales.component";

export const transactions: Routes = [
  {
    path: 'transactions',
    children: [
      {
        path: 'sales',
        component: SalesComponent,
        pathMatch: 'full'
      }, {
        path: 'cashSales',
        component: SalesComponent,
        pathMatch: 'full'
      }
    ]
  }
];
